header{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}


.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}

/* CTA */
.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
    align-items: center;
}

/* HEADER SOCIALS */
.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__socials::after{
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/* Me */
.me{
    background: linear-gradient(var(--color-primary), transparent);
    width: 20rem;
    height: 30rem;
    /* position: absolute; */
    /* left: calc(50%, -11rem); */
    margin-top: 0rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 2rem;

    
    position:relative;
    top:50%;
    left:50%;
    transform:translate(-50%, -50%);
}

/* Scroll Down */
.scroll__down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}


/* MEDIA QUERIES (MEDIUM DEVICES) */
@media screen and (max-width: 1024px){
    .header{
        height: 100vh;
    }
}


/* MEDIA QUERIES (SMALL DEVICES) */
@media screen and (max-width: 600){
    .height{
       height: 70vh;
    }

.header__socials, .scroll__down{
    display: none;
}

}